/* eslint-disable no-loops/no-loops */
/* eslint-disable no-restricted-syntax */
/* eslint-disable no-use-before-define */
import React from 'react'
import PropTypes from 'prop-types'
import { get } from 'lodash'
import styled from 'styled-components'
import { space, width, height, justifyContent } from 'styled-system'
import theme from '@elliemae/ds-system/theme'
import { fixSpaceGutter, mapGap, mapTemplateGrid } from '@elliemae/ds-system'

const GridItem = styled.div`  
  display: grid;
  ${justifyContent}
  ${space}
  ${height}
  ${width}
  ${props => {
    if (typeof props.cols === 'object' && !Array.isArray(props.cols)) {
      let data = ''
      for (const key in theme.mediaQueries) {
        // using grid approach
        if (props.cols[key] && Array.isArray(props.cols[key])) {
          data += `@media ${theme.mediaQueries[key]} {
            grid-template-columns: ${mapTemplateGrid(props.cols[key]).join(' ') || 'auto'};
            grid-template-columns: ${mapTemplateGrid(props.cols[key]).map(c => `minmax(0, ${c})`).join(' ') || 'auto'};
          }`
        } else if (props.isSpanParent) {
          data += `@media ${theme.mediaQueries[key]} {
              grid-template-columns: repeat(${props.cols[key]}, 1fr);
            }`
        }
      }
      return data
    }
    if (Array.isArray(props.cols)) {
      return `
      grid-template-columns: ${mapTemplateGrid(props.cols).join(' ') || 'auto'};
      grid-template-columns: ${mapTemplateGrid(props.cols).map(c => `minmax(0, ${c})`).join(' ') || 'auto'};
      `
    }
    if (props.isSpanParent) {
      return `grid-template-columns: repeat(${props.cols}, 1fr);`
    }
    return ''
  }}
  ${props => {
    if (!props.isSpanParent) return ''
    if (typeof props.span === 'object') {
      let data = ''
      for (const key in theme.mediaQueries) {
        if (props.span[key] && theme.mediaQueries[key]) {
          data += `@media ${theme.mediaQueries[key]} {
            grid-column: auto / span ${props.span[key]};
          }`
        }
      }
      return data
    }
    return `grid-column: auto / span ${props.span};`
  }}  
  grid-template-rows: ${props => mapTemplateGrid(props.rows).join(' ') || 'auto'};
  column-gap: ${props => mapGap(props.gutter)};
  row-gap: ${props => mapGap(props.gutter)};
`

const Grid = React.forwardRef(
  (
    {
      rows = [],
      cols = [],
      alignItems = 'flex-start',
      children,
      className = '',
      direction = 'row',
      justify = 'flex-start',
      gutter = 0,
      wrap = 'wrap',
      ...rest
    },
    ref
  ) => {
    let isSpanParent = true
    const renderChildren = React.Children.map(children, child => {
      if (
        get(child, 'child.type.name') === 'Grid' ||
        get(child, 'type.displayName') === 'Grid' ||
        parseInt(get(child, 'props.span', 0), 10) > 0
      ) {
        const { width: wc, height: hc, span } = child.props
        if (!span) isSpanParent = false
        return React.cloneElement(child, {
          ...child.props,
          width: fixSpaceGutter(wc),
          height: fixSpaceGutter(hc)
        })
      }
      return child
    })
    return (
      <GridItem
        alignItems={alignItems}
        className={className}
        cols={cols}
        direction={direction}
        gutter={gutter}
        isSpanParent={isSpanParent}
        justify={justify}
        rows={rows}
        wrap={wrap}
        {...rest}
        ref={ref}
      >
        {renderChildren}
      </GridItem>
    )
  }
)

Grid.displayName = 'Grid'

Grid.propTypes = {
  rows: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.number, PropTypes.string)),
  cols: PropTypes.shape({}),
  span: PropTypes.number,
  alignItems: PropTypes.oneOf(['flex-start', 'center', 'flex-end', 'stretch', 'baseline']),
  children: PropTypes.node,
  className: PropTypes.string,
  direction: PropTypes.oneOf(['row', 'row-reverse', 'column', 'column-reverse']),
  justify: PropTypes.oneOf(['flex-start', 'center', 'flex-end', 'space-between', 'space-around', 'space-evenly']),
  gutter: PropTypes.oneOf(theme.space),
  wrap: PropTypes.oneOf(['nowrap', 'wrap', 'wrap-reverse'])
}

export default Grid
